import React, { Component } from 'react'
import { connect } from 'react-redux'
import { IFRAME_URL, IS_API_ACCESS_LIVE, IS_TAGEMBED } from '../../../../../../actions/api';
import { FontAwesomeIcon } from '../../../../../../Assets/AssetsHelper';
import { Row, Col, InputGroup, Form, OverlayTrigger, Tooltip, Button } from 'react-bootstrap';
import Select from 'react-select';
import ShareCodeModal from '../../../CommonComponents/ShareCodeModal';
import { COPY_TO_CLIPBOARD } from '../../../../../../utils';

export const NumberBullet = ({ count }) => <span className='d-inline-block text-center h-20px w-20px rounded-circle me-2 bg-success text-gray-100 fs-9 flex-shrink-0' style={{ lineHeight: '20px' }}>{count}</span>

const getEmbedCode = (galleryType, activeEmbed, width, height, wUnit, hUnit, dataTag, allTags, source) => {
    const websiteId = localStorage.getItem('website_id') ?? 0;
    let style = `width:${width}${wUnit.label};height:${height}${hUnit.label};overflow:auto;`;
    let embedUrl =  IS_API_ACCESS_LIVE ? `${IFRAME_URL}embed.min.js` :  IS_TAGEMBED ? `https://widget-test.tagembed.com/embed.min.js` : `https://test.taggbox.com/embed-widget/js/embed-lite-opt.min.js`;
    let container = "taggbox";
    if (IS_TAGEMBED) container = "tagembed-widget";
    if (activeEmbed == 2) style += `border:none;`;
    if(IS_API_ACCESS_LIVE && localStorage.getItem('ownerId') == 1761673) embedUrl = `https://widget.tagshop.ai/khaadi/embed-lite.min.js`;

    return galleryType == 1 ?
        activeEmbed == 1 ? `<div class="${container}" style="${style}" data-widget-id="${websiteId}" website="1"></div><script src="${embedUrl}" type="text/javascript"></script>` : `<iframe src="${IFRAME_URL}${websiteId}?website=1" style="${style}"></iframe>`
        : galleryType == 2 ?
            activeEmbed == 1 ?
                `<div class="${container}" style="${style}" data-widget-id="${websiteId}" website="1" data-filter-type="product" data-filter-id="${source == 'HTML' ? '0' : '{{product.id}}'}"></div><script src="${embedUrl}" type="text/javascript"></script>` : `<iframe src="${IFRAME_URL}${websiteId}?website=1&data-filter-type=product&data-filter-id={{product.id}}" style="${style}"></iframe>`
            : galleryType == 3 ?
                activeEmbed == 1 ?
                    `<div class="${container}" style="${style}" data-widget-id="${websiteId}" website="1" data-tags="${dataTag ? 'true' : 'false'}" data-keywords="${(allTags?.length && dataTag) ? allTags.join(",") : ""}"></div><script src="${embedUrl}" type="text/javascript"></script>` : `<iframe src="${IFRAME_URL}${websiteId}?website=1&tags=${dataTag ? 'true' : 'false'}&data-keywords=${allTags?.length ? allTags.join('%2C') : ''}" style="${style}"></iframe>`
                : null
}
class EmbedCode extends Component {
    state = { width: 100, height: 100, widthPr: '%', heightPr: '%', copied: false, wUnit: { value: 'percent', label: '%' }, hUnit: { value: 'percent', label: '%' }, searchData: [], isLoading: false, searchPost: null, openDropdwn: false, isPreview: false, copied: false, activeEmbed:1 }

    copyText = (e) => {
        var copyText = document.getElementById('wallembedareaLiteCode03').value
        navigator.clipboard.writeText(copyText);
        this.setState({ copied: true })
        setTimeout(() => { this.setState({ copied: false }) }, 1000)
    }

    changeSizeUnit = (type, val, type2, num) => this.setState({ [type]: val, [type2]: val.label == '%' && Number(num) > 100 ? 100 : num })

    changeSize = (e, type, unit) => {
        const val = Math.max(0, unit.label === '%' ? Math.min(100, e.target.value) : e.target.value);
        this.setState({ [type]: val })
    }

    setOpenPopup = () => this.setState({ openPopup: false })

    copyText = (id) => {
        COPY_TO_CLIPBOARD(id)
        this.setState({ copied: true })
        setTimeout(() => { this.setState({ copied: false }) }, 1000)
    }
    render() {
        const { width, height, wUnit, openPopup, copied, hUnit, activeEmbed } = this.state
        const { platform, galleryType, allTags, count, dataTag, source } = this.props
        var embedCount = count
        const options = [
            { value: 'percent', label: '%' },
            { value: 'pixel', label: 'px' }
        ]
        return (<>
            <p className="h6 mb-4 d-flex"><NumberBullet count={embedCount} />Adjust the width and height of the Gallery as per your requirement</p>
            <Row className='embed_size_ mb-5' style={{ maxWidth: 991 }}>
                <Col sm={6}>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id="width_">Width</InputGroup.Text>
                        <Form.Control type='number' max={wUnit.label == '%' ? "100" : ''} min={0} value={width} onChange={(e) => this.changeSize(e, 'width', wUnit)} />
                        <InputGroup.Text className="p-0 ps-1 bg-transparent">
                            <Select className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" value={wUnit} isSearchable={false} options={options} onChange={(e) => this.changeSizeUnit('wUnit', e, 'width', width)} />
                        </InputGroup.Text>
                    </InputGroup>
                </Col>
                <Col sm={6}>
                    <InputGroup className="mb-3">
                        <InputGroup.Text id="height_">Height</InputGroup.Text>
                        <Form.Control type='number' max={hUnit.label == '%' ? "100" : ''} min={0} value={height} onChange={(e) => this.changeSize(e, 'height', hUnit)} />
                        <InputGroup.Text className="p-0 ps-1 bg-transparent">
                            <Select className={`selectpicker selectpicker-sm`} classNamePrefix="selectpicker" value={hUnit} isSearchable={false} options={options} onChange={(e) => this.changeSizeUnit('hUnit', e, 'height', height)} />
                        </InputGroup.Text>
                    </InputGroup>
                </Col>
            </Row>

            {galleryType == 2 ?
                <div className='mb-7'>
                    <p className="h6 mb-4 d-flex"><NumberBullet count={embedCount + 1} />Add your Product ID identifier variable after the Data Filter ID section in the publish code in Step 3</p>
                    <div className='ps-8'>
                        <p>For Example: <span className='text-muted'>{`data-filter-id="{{product-id}}"`}</span></p>
                    </div>
                </div>
                : null}

            <p className="h6 mb-4 d-flex"><NumberBullet count={embedCount + (galleryType == 2 ? 2 : 1)} />
                {platform == 1 ?
                    'Copy the generated Publish Code for your HTML gallery'
                    :
                    platform == 2 ?
                        'Copy the below given Publish Code for your Shopify gallery'
                        :
                        platform == 3 ?
                            'Copy the generated Publish Code for your WordPress gallery'
                            :
                            platform == 4 ?
                                'Copy the generated Publish Code for your Prestashop gallery'
                                :
                                platform == 5 ?
                                    'Copy the generated Publish Code for your Bigcommerce gallery'
                                    :
                                    platform == 6 ?
                                        'Copy the generated Publish Code for your Magento gallery'
                                        :
                                        platform == 7 ?
                                            'Copy the generated Publish Code for your gallery'
                                            :
                                            platform == 8 ?
                                            'Copy the generated Publish Code for your Wix gallery'
                                            :
                                            platform == 9 ?
                                            'Copy the generated Publish Code for your SquareSpace gallery'
                                            : ''
                }
            </p>
            <div className="position-relative mb-7 embed_code embed_code_box iframe_embcode_box rounded-3">
                <div className='embed-border p-2'>
                    <div className="embed_code_wrap">
                        <div className="nav-ul mb-2">
                            <button className={`btn btn-sm me-1 ${activeEmbed == 1 ? 'btn-primary' : ''}`} onClick={()=>this.setState({activeEmbed:1})}>Litecode</button>
                            <button className={`btn btn-sm ${activeEmbed == 2 ? 'btn-primary' : ''}`} onClick={()=>this.setState({activeEmbed:2})}>Iframe</button>
                        </div>
                        <code className="form-control textarea-min-row-4 p-lg-4 fs-9" id="embed_code_" onClick={() => { this.copyText('embed_code_') }}>{getEmbedCode(galleryType, activeEmbed, width, height, wUnit, hUnit, dataTag, allTags, source)}</code>
                    </div>
                </div>
                <div className="position-absolute end-0 bottom-0 pe-5 pb-5">
                    <OverlayTrigger placement="top" overlay={<Tooltip>{!copied ? 'Copy Code' : 'Code Copied'}</Tooltip>}>
                        <Button variant="link" className="text-gray-300 fs-6 p-0 me-2" onClick={() => this.copyText('embed_code_')}>
                            <FontAwesomeIcon type={'solid'} icon={!copied ? 'copy' : 'check'} iconClass={copied ? 'text-success' : ''} />
                        </Button>
                    </OverlayTrigger>
                    <OverlayTrigger placement="top" overlay={<Tooltip>Share Code</Tooltip>}>
                        <Button variant="link" className="text-gray-300 fs-6 GetShareCode" onClick={(e) => this.setState({ openPopup: true })}>
                            <FontAwesomeIcon type="solid" icon={'envelope'} iconClass="me-0" />
                        </Button>
                    </OverlayTrigger>
                </div>
            </div>
            {openPopup ? <ShareCodeModal type={4} text="Share this embed code with your team. Enter the email Id to send the code directly to them" heading="Send code by email" onClose={(e) => this.setState({ openPopup: false })} codeId={'#embed_code_'} /> : null}
        </>
        )
    }
}


const mapStateToProps = (state) => {
    return {
        userData: state.authorized?.userData?.user_data,
        appData: state.appData,
    }
}

export default connect(mapStateToProps)(EmbedCode);